// Material UI
import Divider from '@mui/material/Divider';

// Owned
import Info from './Info';
import LimitedFunctionality from './LimitedFunctionality';
import * as Constants from '../../Shared/Constants'
import '../App.css';

// External
import React from 'react';

export default function ContentBody(props) {
  switch (props.isRunning) {
    case true:
      return (
        <div className="content">
          {props.loggedIn === Constants.sessionState.LOGGED_IN ?
              <Info setHaveInfo={props.setHaveInfo} />
              : 
              props.loggedIn === Constants.sessionState.NOT_LOGGED_IN ?
              <div>
                {props.hostname !== "" ?
                  <div>
                    <div className="info">
                      <Info setHaveInfo={props.setHaveInfo} />
                    </div>
                    <Divider />
                  </div>
                  :
                  null
                }
                <div className="limited">
                  <LimitedFunctionality fullText={!props.haveInfo} />
                </div>
              </div>
              : null
          }
        </div>
      )
    default:
      return (null)
  }
}