// Material UI
import Grid from '@mui/material/Grid';
import CircularProgress from '@mui/material/CircularProgress';

// Owned
import DataCard from '../../Shared/View/DataCard';
import { Request, Messages } from '../../../shared';
import { Tools } from '../../../Tools/Tools';

// External
import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { NoDetection } from './NoDetection';
import { HaveResults } from './HaveResults';

export default function Info(props) {
    const { t } = useTranslation();

    const [loading, setLoading] = useState(true);

    const [executionResults, setExecutionResults] = useState({
        buttonsClicked: 0
    });

    const [lastLog, setLastLog] = useState(null);

    const getLogs = async (hostname) => {
        try {
            const results = await new Request(Messages.Logger.GetLogs).sendRequest();
            const logs = results.message.payload.logs;
            const previousLog = logs.filter(l => l.hostname === hostname)[0];
            if (previousLog) {
                props.setHaveInfo(true);
                setLastLog(previousLog);
            }            
        } catch(e) {
            setLastLog(null);
        }
    }

    const getExecutionResults = async (tabId, hostname) => {
        new Request(Messages.Tab.GetExecutionResults, { tabId }).sendRequest()
        .then((results) => {
            const executionResults = results.message.payload.executionResults;
            const parsedResults = parseExecutionResults(executionResults, hostname);
            setExecutionResults(parsedResults);

            props.setHaveInfo(true);
            return parsedResults;
        })
        .catch( (e) => {
            
        });
    }

    const init = async () => {
        try {
            const result = await new Request(Messages.Tab.GetActiveTab).sendRequest();
            const tab = result.message.payload.tab;
            const tabId = tab.id;

            const hostname = Tools.hostnameForURL(tab.url);
            
            await getExecutionResults(tabId, hostname);
            await getLogs(hostname);
            
            setLoading(false);
        } catch(e) {
            setLoading(false);
        }
        
    }

    const parseExecutionResults = (input, hostname) => {
        let results = {
            outcome: "",
            buttonsClicked: 0
        };

        if (input.outcome === "PROCESSING") {
            results.outcome = t('popup.outcome.processing.title');
            results.actionTaken = true;
            results.processing = true;
            return results;
        }

        if (input.outcome === "NONE") {
            results.outcome = t('popup.outcome.none.title');
            results.actionTaken = false;
            results.processing = false;
            return results;
        }

        switch (input.outcome) {
            case "PREFERENCES_APPLIED":
                results.outcome = t('popup.outcome.preferences_applied.title', { hostname: hostname });
                results.actionTaken = true;
                results.processing = false;
                break;
            case "ACCEPTED_DEFAULT":
                results.outcome = t('popup.outcome.accepted_default.title', { hostname: hostname });
                results.actionTaken = true;
                results.processing = false;
                break;
            case "REJECTED_ALL":
                results.outcome = t('popup.outcome.reject_all.title', { hostname: hostname });;
                results.actionTaken = true;
                results.processing = false;
                break;
            case "ACCEPTED_NECESSARY":
                results.outcome = t('popup.outcome.accepted_necessary.title', { hostname: hostname });
                results.actionTaken = true;
                results.processing = false;
                break;
                
            default :
                results.outcome = "";
                results.actionTaken = false;
                results.processing = false;
                break;
        }

        results.buttonsClicked = input.totalClicks;
        results.hostname = input.hostname;

        return results;
    }

    const LastLog = () => {
        let text = "";

        switch (lastLog.outcome) {
            case "PREFERENCES_APPLIED":
                text = t('popup.last_log.preferences_applied', { hostname: lastLog.hostname, date: lastLog.date, time: lastLog.time });
                break;  
            case "ACCEPTED_DEFAULT":
                    text = t('popup.last_log.accepted_default', { hostname: lastLog.hostname, date: lastLog.date, time: lastLog.time });
                    break;
            case "REJECTED_ALL":
                    text = t('popup.last_log.reject_all', { hostname: lastLog.hostname, date: lastLog.date, time: lastLog.time });
                    break;
            case "ACCEPTED_NECESSARY":
                    text = t('popup.last_log.accepted_necessary', { hostname: lastLog.hostname, date: lastLog.date, time: lastLog.time });
                    break;
            default :
                    text = null;
                    break;
        }

        return(
            <div>
                <Grid container spacing={1} alignItems="center" direction="column" justify="center">
                    <Grid item xs={12}>
                        {text}
                    </Grid>
                    <Grid item xs={12}>
                        <div style={{margin: "5px"}}>
                            <DataCard 
                                number={lastLog.totalClicks ? lastLog.totalClicks : 0} 
                                description="Clicks Saved" 
                                info={t('popup.clicks_saved.info')}
                            />
                        </div>
                    </Grid>
                </Grid>
            </div>
        )
    }

    useEffect( () => {
        init();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    if (loading) {
        return (
            <CircularProgress/>
        )
    }

    if (executionResults.buttonsClicked > 0) {
        return <HaveResults {...executionResults} />;
    }

    if (lastLog) {
        return (
            <div>
                <LastLog/>
            </div>
        )
    }

    return (
        <NoDetection/>
    )
}
