// Material UI
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

// Styles
import { makeStyles } from '@mui/styles';

// Owned
import { Request, Messages } from '../../../shared';
import '../App.css';

// External
import React, { useCallback, useState } from 'react';
import { useTranslation } from 'react-i18next';
import Divider from '@mui/material/Divider';
import { processUrl, stringToDOM } from '../../../CookeBannerParser';

const useStyles = makeStyles((theme) => ({
    root: {
        verticalAlign: "center",
        marginTop: '10px'
    },
    box: {
        marginTop: -9,
        textAlign: 'right'
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
    backButtonBox: {
        textAlign: 'left',
        marginLeft: 14,
        marginTop: 10
    },
    reasonTextField: {
        width: '100%'
    },
    bottomDivider: {
        marginTop: 5,
        marginBottom: 0,
    },
    submitButton: {
        marginTop: 24,
    }
}));

export default function ReportProblem({ hostname, setSuccessMessage, setErrorMessage, isReportingProblem, tab }) {
    const classes = useStyles();
    const { t } = useTranslation();

    const [optionSelected, setOptionSelected] = React.useState('cookie_popups');
    const [isOtherReason, setIsOtherReason] = useState(false);
    const [sendReportLoading, setSendReportLoading] = useState(false);
    const [reasonDescription, setReasonDescription] = useState('');
    const [sendButtonState, setSendButtonState] = useState(optionSelected !== 'other');

    const updateButtonState = useCallback((option, description) => {
        const buttonState = (option !== 'other' || (option === 'other' && description !== ''));

        setSendButtonState(buttonState);
    }, []);

    const handleOtherReasonChange = useCallback((event) => {
        setReasonDescription(event.target.value);

        updateButtonState(optionSelected, event.target.value);
    }, [optionSelected, updateButtonState]);

    const handleChange = useCallback((event) => {
        const optionSelected = event.target.value;

        setOptionSelected(optionSelected);
        setIsOtherReason(optionSelected === 'other');

        updateButtonState(optionSelected, reasonDescription);
    }, [updateButtonState, reasonDescription]);

    const getCookieHtml = useCallback(async () => {
        try {
            const getDocumentResult = await new Request(Messages.Content.Dom.GetDocument).sendRequestToTab(tab.id);
            const documentHtml = getDocumentResult.message.payload.documentHtml;
            const tabDocument = stringToDOM(documentHtml);

            return processUrl(hostname, tabDocument).htmlBlocks;
        } catch (e) {
            return [];
        }
    }, [hostname, tab]);

    const reportWebsite = useCallback(async (reason, description) => {
        setSendReportLoading(true);

        const cookieHTMLs = await getCookieHtml()

        new Request(Messages.Report.ReportWebsite, { hostname, reason, description, cookieHTMLs })
            .sendRequest()
            .then(() => {
                setSendReportLoading(false);
                setReasonDescription('');
                setSuccessMessage(t('popup.report.success_message'));
            })
            .catch(() => {
                setSendReportLoading(false);
                setErrorMessage(t('popup.report.error_message'));
            });
    }, [getCookieHtml, hostname, setSuccessMessage, t, setErrorMessage]);

    const handleSubmit = useCallback((event) => {
        event.preventDefault();

        const description = optionSelected === 'other' ? reasonDescription : null;

        reportWebsite(optionSelected, description);
    }, [reportWebsite, optionSelected, reasonDescription]);

    if (!isReportingProblem) {
        return null;
    }

    return (
        <>
            <Box className={classes.box}>
                <form onSubmit={handleSubmit}>
                    <RadioGroup aria-label="problem" name="problem" value={optionSelected} onChange={handleChange}>
                        <FormControlLabel value="cookie_popups" control={<Radio/>}
                                          label={t('popup.report.radiogroup.option1')}/>
                        <FormControlLabel value="something_stop_working" control={<Radio/>}
                                          label={t('popup.report.radiogroup.option2')}/>
                        <FormControlLabel value="other" control={<Radio/>}
                                          label={t('popup.report.radiogroup.option3')}/>
                    </RadioGroup>
                    {isOtherReason ?
                        <TextField className={classes.reasonTextField} autoComplete="off" value={reasonDescription}
                                   onChange={handleOtherReasonChange} label={t('popup.report.textfield')}
                                   id="outlined-secondary" variant="filled" rows={1}/> : null}
                    <Divider className={classes.bottomDivider} />
                    <Button className={classes.submitButton}
                            disabled={!sendButtonState}
                            color="primary"
                            variant="contained"
                            type="submit">
                        Send
                    </Button>
                </form>
            </Box>
            <Backdrop className={classes.backdrop} open={sendReportLoading}>
                <CircularProgress color="inherit"/>
            </Backdrop>
        </>
    );
}
