// Material UI
import Switch from '@mui/material/Switch';
import Divider from '@mui/material/Divider';
import Button from '@mui/material/Button';

// Styles
import { makeStyles } from '@mui/styles';

// Owned
import '../App.css';
import { Request, Messages } from '../../../shared';
import { Tools } from '../../../Tools/Tools';

// External
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import BrowserStorage from '../../../Background/BackgroundTools/BrowserStorage';
import { StorageKeys } from '../../../Background/BackgroundTools/StorageKeys';
import Typography from '@mui/material/Typography';
import { TrashIcon } from '../../Shared/Icons/TrashIcon/TrashIcon';

const useStyles = makeStyles(() => ({
    hostnameStatus: {
        display: 'flex',
        justifyContent: 'space-between',
    },
    hostnameData: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start',
    },
    deleteButton: {
        marginTop: 8,
    }
}));

export default function HostStateSwitch(props) {
    const { hostname, isRunning, setIsRunning, tabId, isAdmin } = props;
    const { t } = useTranslation();
    const classes = useStyles();

    const handleDeleteCache = useCallback(() => {
        BrowserStorage.clearKey(StorageKeys.Provider.ProviderDetectors);
        BrowserStorage.clearKey(StorageKeys.Provider.Rules);
    }, []);

    const handleDeleteDEK = useCallback(async () => {
        try {
            await new Request(Messages.KeyManager.DeleteDEK).sendRequest();
        } catch(e) {

        }
    }, []);

    const handleChange = useCallback((event) => {
        const state = event.target.checked;

        setIsRunning(state);
    
        new Request(Messages.Whitelist.UpdateHostnameState, { hostname, state })
            .sendRequest()
            .then(() => {
                chrome.tabs.reload(tabId)
            }).catch(e => {
                Tools.debugLog("Error: " );
                Tools.debugLog(e, true);
            });

    }, [hostname, setIsRunning, tabId]);

    if (!hostname) {
        return null;
    }

    return (
        <>
            <div className={classes.hostnameStatus}>
                <div className={classes.hostnameData}>
                    <Typography>{ isRunning ? t('general.enabled') : t('general.disabled') }</Typography>
                    <Typography variant="body2" textAlign="start">{ t('general.in') + " " + hostname }</Typography>
                </div>
                <Switch onChange={handleChange} checked={isRunning} />
            </div>
            <Divider />
            {
                isAdmin &&
                <div>
                    <Button variant="contained" color="primary" onClick={handleDeleteCache} className={classes.deleteButton} startIcon={<TrashIcon/>}>
                        Delete Provider Info
                    </Button>
                    <Button variant="contained" color="primary" onClick={handleDeleteDEK} className={classes.deleteButton} startIcon={<TrashIcon/>}>
                        Delete DEK
                    </Button>
                    <Divider />
                </div>
            }
        </>
    );
}
