import React from 'react';
import BrowserHelper from '../../../shared/BrowserHelper';
import Button from '@mui/material/Button';
import Link from '@mui/material/Link';
import { openNativeApp } from '../../Shared/Functions/openNativeApp';
import { useTranslation } from 'react-i18next';
import { useHasEntitlement } from '../../Shared/Hook/useHasEntitlement';
import Typography from '@mui/material/Typography';

const OpenMobileApp = () => {
    const { hasEntitlement, isRCIdSynced } = useHasEntitlement()
    const isMobileSafari = BrowserHelper.isMobileSafari(navigator.userAgent);
    const { t } = useTranslation();

    if (!isMobileSafari) {
        return null;
    }

    return (
        <div>
            {hasEntitlement && !isRCIdSynced && <Typography>Please open app to update</Typography>}
            <Button
                target="_blank"
                component={Link}
                href="superagent://"
                color="primary"
                variant="contained"
                style={{ marginTop: "10px" }}
                size="small"
                onClick={openNativeApp}
            >
                {t('popup.open_app.button')}
            </Button>
        </div>
    );
}

export default OpenMobileApp;
