import { useCallback, useEffect, useMemo, useState } from 'react';
import { isDev } from '../../../Tools/IsDev';
import { EntitlementConstants } from '../../../Background/Entitlement/Constants';
import { Messages, Request } from '../../../shared';
import { useHasEntitlement } from '../../Shared/Hook/useHasEntitlement';
import { isEligible } from '../../Shared/Functions/isEligible';
import Button from '@mui/material/Button';

export const DebugInfo = () => {
    const { hasEntitlement, isRCIdSynced, installedAt } = useHasEntitlement()
    const [freeRuns, setFreeRuns] = useState(0)

    useEffect(() => {
        const load = async () => {
            const counterResult = await new Request(Messages.Entitlement.GetCounter, {}).sendRequest();
            setFreeRuns(counterResult.message.payload.counter)
        };
        load()
    }, [])

    const isEligibleValue = useMemo(
        () => isEligible(hasEntitlement, isRCIdSynced, installedAt),
        [hasEntitlement, isRCIdSynced, installedAt]
    );

    const showUpdatePopup = useCallback(() => {
        new Request(Messages.Entitlement.SubscriptionVerificationNotification.ShowPopup, {}).sendRequest();
    }, [])

    if (!isDev()) {
        return null
    }

    return (
        <>
            <div>
                Your subscription: {hasEntitlement ? 'Active' : 'Inactive'}
            </div>
            <div>
                isRCIdSynced: {isRCIdSynced.toString()}
            </div>
            {installedAt && <div>Installed At: {new Date(installedAt).toISOString()}. Expired after 7 days</div>}
            <div>
                Is Eligible: {isEligibleValue.toString()}
            </div>
            <div>
                Free runs: {freeRuns} of {EntitlementConstants.FREE_DAILY}
            </div>
            <Button onClick={showUpdatePopup} type="button" color="primary">Show Update Popup</Button>
        </>
    )
}
