import { SvgIcon } from '@mui/material';
import { ReactComponent as BrandDarkSvg } from './brand-icon-dark.svg';
import { ReactComponent as BrandLightSvg } from './brand-icon-light.svg';
import { useTheme } from '@mui/material/styles';

export const BrandIcon = ({ brand, ...props }) => {
    const theme = useTheme();
    brand = brand || (theme.palette.mode === 'dark' ? 'light' : 'dark');
    const component = brand === 'dark' ? BrandDarkSvg : BrandLightSvg;

    return <SvgIcon {...props} component={component} inheritViewBox />;
}
