// Material UI
import Divider from '@mui/material/Divider';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';

// Owned
import '../App.css';
import * as Constants from '../../Shared/Constants'
import { Request, Messages } from '../../../shared';

// External
import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { CircularProgress } from '@mui/material';

export default function SessionState(props) {

    const { t } = useTranslation();

    const [isLoading, setIsLoading] = useState(true);
    const [failedEntitlement, setFailedEntitlement] = useState(false);
    
    const getExecutionOutcome = async (tabId) => {
        new Request(Messages.Tab.GetExecutionResults, { tabId }).sendRequest()
        .then((results) => {
            const executionResults = results.message.payload.executionResults;
            setFailedEntitlement(executionResults.outcome === "NO_ENTITLEMENT");
            setIsLoading(false);
            return executionResults.outcome;
        })
        .catch( (e) => {
            setIsLoading(false);
            console.log("Error getting execution outcome.");
        });
    }

    const init = async () => {
        try {
            const result = await new Request(Messages.Tab.GetActiveTab).sendRequest();
            const tab = result.message.payload.tab;
            const tabId = tab.id;

            await getExecutionOutcome(tabId);
        } catch(e) {
            setIsLoading(false);
        }
    }

    useEffect( () => {
        init();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    if(isLoading) {
        return(
            <CircularProgress />
        )
    } else if (failedEntitlement) {
        return(
            <div>
            <Grid item xs={12} style={{padding: 10}}>
                <Typography style={{ whiteSpace: "pre-line" }}>
                { t('popup.session_state.no_entitlement')}
                </Typography>
            </Grid>
            <Divider />
        </div>
        )
    } else {
        return (
            <div>
                <Grid item xs={12} style={{padding: 10}}>
                    <Typography style={{ whiteSpace: "pre-line" }}>
                    { props.loggedIn === Constants.sessionState.LOGGED_IN ? t('popup.session_state.logged_in', { email: props.email }) : t('popup.session_state.not_logged_in')}
                    </Typography>
                </Grid>
                <Divider />
            </div>
        );
    }

}
