// eslint-disable-next-line no-unused-vars
/* globals CognitoUser */

export default class GroupValidator {

    /**
     * Checks if a user is in the Admins group
     * @param {CognitoUser} user - CognitoUser - A Cognito user object
     * @return {boolean} True/False
     */
    static isUserAdmin(user) {
        const groups = user.signInUserSession.idToken.payload['cognito:groups'];

        return (groups && groups[0] === 'Admins');
    }
    
}