import React, { useCallback } from 'react';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Tooltip from '@mui/material/Tooltip';
import IconButton from '@mui/material/IconButton';

// Icons
import { pxToRem } from '../Components/SuperAgentThemeProvider/pxToRem';
import { InfoIcon } from '../Icons/InfoIcon/InfoIcon';

export const DataCard = ({ number, description, info, itemsMinWidth = '250px', showTooltip= true, maxWidth = false }) => {
    const applyThemeToPaper = useCallback((theme) => {
        return {
            position: 'relative',
            minHeight: '145px',
            maxHeight: '192px',
            maxWidth: '328px',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            textAlign: 'center',
            borderRadius: '30px',
            backgroundColor: theme.palette.background.default,
            boxShadow: 'none',
            border: `2px solid ${theme.palette.text.primary}`,
            ...(itemsMinWidth && {
                [theme.breakpoints.down('sm')]: {
                    minWidth: itemsMinWidth,
                }
            }),
            ...(maxWidth && {
                width: '100%',
            }),
        };
    }, [itemsMinWidth, maxWidth]);

    return (
        <Paper sx={applyThemeToPaper}>
            <Typography variant='h1' sx={(theme) => ({
                [theme.breakpoints.down('sm')]: {
                    ...(!maxWidth && {
                        fontSize: pxToRem(24),
                    }),
                }
            })}>
                {number}
            </Typography>
            <Typography variant="subtitle1">
                {description}
            </Typography>
            {showTooltip &&
                <Tooltip
                    sx={(theme) => ({
                        [theme.breakpoints.down('xs')]: {
                            display: 'none'
                        }
                    })}
                    title={
                    <Typography variant="body2">
                        {info}
                    </Typography>
                    }>
                    <IconButton aria-label="info" sx={{
                        position: 'absolute',
                        top: 0,
                        right: 0,
                    }}>
                        <InfoIcon />
                    </IconButton>
                </Tooltip>
            }
        </Paper>
    );
}

export default DataCard;
