// Material UI
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
// Owned
import '../App.css';
import { Request, Messages } from '../../../shared';
import awsconfig from '../../../Config/aws-exports';
import awsconfigdev from '../../../Config/aws-exports-devtest';

// External
import React, { useCallback, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { makeStyles } from '@mui/styles';

const useStyles = makeStyles((theme) => ({
    bottom: {
        marginTop: 24,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
    },
    reportProblem: {
        textTransform: 'capitalize',
        color: theme.palette.text.primary,
        textDecoration: 'underline',
        fontSize: theme.typography.body1.fontSize,
        paddingLeft: 0,
    }
}));

export default function ReportProblem({ setIsReportingProblem, isReportingProblem, hostname}) {
    const classes = useStyles();
    const { t } = useTranslation();

    const [version, setVersion] = useState();

    const handleReportProblem = useCallback(() => {
        setIsReportingProblem(true);
    }, [setIsReportingProblem]);

    useEffect(() => {
        const init = async () => {
            try {
                const result = await new Request(Messages.Generic.GetVersion, {}).sendRequest();
                const version = result.message.payload.version;
                setVersion(version);
            } catch(e) {

            }
        }

        init();
    }, []);

    return (
        <div className={classes.bottom}>
            {!isReportingProblem && hostname ?
                <Button className={classes.reportProblem} size="small" variant="text" onClick={handleReportProblem}>
                    {t('popup.app.button.report_problem')}
                </Button>
                :
                <p>&nbsp;</p>
            }
            {version &&
                <Typography variant='body2'>
                    {t('popup.app.version', { version: version })}
                    {awsconfig.aws_user_pools_id === awsconfigdev.aws_user_pools_id ? " (DEV)" : null}
                </Typography>
            }
        </div>
    );
}
