import React from 'react';
import DataCard from '../../Shared/View/DataCard';
import { useTranslation } from 'react-i18next';
import Typography from '@mui/material/Typography';
import { makeStyles } from '@mui/styles';

const useStyles = makeStyles(() => ({
    actionTakenResults: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'start',
    },
    outcome: {
        marginBottom: 24,
    }
}));

export const HaveResults = ({
    actionTaken,
    outcome,
    processing,
    buttonsClicked,
}) => {
    const { t } = useTranslation();
    const classes = useStyles();

    if (!actionTaken) {
        return null;
    }

    return (
        <div className={classes.actionTakenResults}>
            <Typography className={classes.outcome} variant="body1" textAlign="start">{outcome}</Typography>
            {!processing &&
                <DataCard
                    number={buttonsClicked || 0}
                    description="Clicks Saved"
                    info={t('popup.clicks_saved.info')}
                    showTooltip={false}
                />
            }
        </div>
    )
}
