// External
import React from 'react';
import { useTranslation } from 'react-i18next';

// Material UI
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Alert from '@mui/material/Alert';
import Link from '@mui/material/Link';

// Icons
import DoneIcon from '@mui/icons-material/Done';

// Owned
import { Request, Messages } from '../../../shared';

export default function LimitedFunctionality(props) {

    const { t } = useTranslation();

    const openSignIn = () => {
        const request = new Request(Messages.Auth.OpenSignIn, {});
        request.sendRequest();
    }

    const openSignUp = () => {
        const request = new Request(Messages.Auth.OpenSignUp, {});
        request.sendRequest();
    }

    return(
        <div>
            <Grid container spacing={2} direction="column" alignItems="center" justifyContent="center">
                <Grid item xs={12}>
                    <Alert color="success">
                        {t('popup.limited_functionality.alert')}
                    </Alert>
                </Grid>
                {props.fullText ?
                    <div>
                        <Grid item xs={12}>
                            <Typography style={{ fontWeight: 600 }}>
                                {t('popup.limited_functionality.body')}
                            </Typography>
                        </Grid>
                        <Grid item xs={12} style={{margin: '10px'}}>
                            <Benefit text={t('popup.limited_functionality.benefits.first')} />
                            <Benefit text={t('popup.limited_functionality.benefits.second')} />
                            <Benefit text={t('popup.limited_functionality.benefits.third')} />
                        </Grid>
                    </div>
                    :
                    null
                }
                <Grid item xs={12}>
                    <Button style={{marginRight: "10px"}} color="primary" variant="contained" onClick={() => { openSignUp(); }}>
                        {t('popup.limited_functionality.button.signup')}
                    </Button>
                    <Link style={{marginLeft: "10px"}} href="#" underline="always" onClick={() => { openSignIn(); }}>
                        {t('popup.limited_functionality.button.signin')}
                    </Link>
                </Grid>
            </Grid>
        </div>
        
    );
}

const Benefit = (props) => {
    return(
        <div style={{
            display: 'flex',
            alignItems: 'center',
            flexWrap: 'wrap',
        }}>
            <DoneIcon fontSize="small" style={{marginRight: "5px"}} />
            <span>{props.text}</span>
        </div>  
    )
}