// Material UI
import Link from '@mui/material/Link';
import Divider from '@mui/material/Divider';
import Tooltip from '@mui/material/Tooltip';

// Owned
import '../App.css';
import { Request, Messages } from '../../../shared';

// External
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { openMainSite } from '../../Shared/Functions/openMainSite';
import { openOptions } from '../../Shared/Functions/openOptions';
import { SettingsIcon } from '../../Shared/Icons/SettingsIcon/SettingsIcon';
import { TrashIcon } from '../../Shared/Icons/TrashIcon/TrashIcon';
import { SuperAgentIcon } from '../../Shared/Icons/SuperAgentIcon/SuperAgentIcon';
import { BrandIcon } from '../../Shared/Icons/BrandIcon/BrandIcon';
import { makeStyles } from '@mui/styles';

const useStyles = makeStyles((theme) => ({
    bar: {
        display: 'flex',
        justifyContent: 'space-between',
        marginBottom: 24,
    },
    barLogoLink: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        lineHeight: 0,
    },
    barIcon: {
        float: 'right',
        textAlign: 'right',
        width: 100,
    },
    brand: {
        fontSize: 40,
    },
    superagent: {
        fontSize: 124,
        height: 'auto',
        color: theme.palette.text.primary,
    },
    actions: {
        display: 'flex',
        alignItems: 'center',
    },
    deleteIcon: {
        fontSize: 18,
    },
    settingsIcon: {
        fontSize: 20,
    },
    spacer: {
        marginRight: 10,
    }
}));

export default function NavigationBar(props) {
    const classes = useStyles();
    const { hostname, setSuccessMessage } = props;
    const { t } = useTranslation();

    const deleteCookies = useCallback(() => {
        new Request(Messages.Cookies.DeleteCookies, { hostname })
            .sendRequest()
            .then(() => setSuccessMessage(t('popup.navigation.cookies_deleted')));
    }, [hostname, setSuccessMessage, t]);

    return (
        <>
            <div className={classes.bar}>
                <Link className={classes.barLogoLink} href="#" onClick={openMainSite}>
                    <BrandIcon className={classes.brand} />
                </Link>
                <Link className={classes.barLogoLink} href="#" onClick={openMainSite}>
                    <SuperAgentIcon className={classes.superagent} />
                </Link>
                <div className={classes.actions}>
                    <Tooltip title={t('popup.navigation.delete_cookies')} className={classes.spacer}>
                        <Link href="#" onClick={deleteCookies} color="inherit">
                            <TrashIcon className={classes.deleteIcon} />
                        </Link>
                    </Tooltip>
                    <Tooltip title={t('popup.navigation.settings')}>
                        <Link href="#" onClick={openOptions} color="inherit">
                            <SettingsIcon className={classes.settingsIcon} />
                        </Link>
                    </Tooltip>
                </div>
            </div>
            <Divider/>
        </>
    );
}
