import { useEffect, useState } from 'react';
import { Messages, Request } from '../../../shared';

export const useHasEntitlement = () => {
    const [hasEntitlement, setEntitlement] = useState(false)
    const [isRCIdSynced, setIsRCIdSynced] = useState(false)
    const [installedAt, setInstalledAt] = useState(null)

    useEffect(() => {
        const load = async () => {
            const hasEntitlementResult = await new Request(Messages.Entitlement.GetCurrentEntitlement, {}).sendRequest();
            setEntitlement(hasEntitlementResult.message.payload.hasEntitlement)
            setIsRCIdSynced(hasEntitlementResult.message.payload.isRCIdSynced)
            setInstalledAt(hasEntitlementResult.message.payload.installedAt)
        };
        load()
    }, [])

    return { hasEntitlement, isRCIdSynced, installedAt }
}
