// Material UI
import Alert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';

// External
import React from 'react';

export default function Alerts(props) {
    return(
        <div>
            <Snackbar open={props.error} autoHideDuration={4000} onClose={() => { props.setErrorMessage(null); }}>
                <Alert severity="error">{props.error}</Alert>
            </Snackbar>

            <Snackbar open={props.success} autoHideDuration={4000} onClose={() => { props.setSuccessMessage(null); }}>
                <Alert severity="success">{props.success}</Alert>
            </Snackbar>
        </div>
    )
}