export function processUrl(
    url,
    document,
    searchedWord = 'cookie|gdpr|policy',
    searchedAttributes = 'cookie|gdpr|policy|axeptio|uc-overlay',
    timeoutDelay = 5000
  ) {
    const regularGeneralExpression = new RegExp('.*' + searchedWord + '.*', 'i');
    const regularAttributesExpression = new RegExp('.*' + searchedAttributes + '.*', 'i');
    const skipHtmlElements = ['script', 'style', 'html', 'body', 'footer', 'head'];

    // Getting page source
    const blocks = Array.from(document.querySelectorAll('*')).filter(element => regularGeneralExpression.test(element.textContent));

    const parentTexts = [];
    const parentHtmls = [];
    const tags = [];
    const documentElements = []

    for (const block of blocks) {
        // Attempt to find the best parent
        let theBestParent = block.parentElement;
        while (theBestParent && !searchParentCriteria(theBestParent, regularAttributesExpression)) {
            theBestParent = theBestParent.parentElement;
        }

        const parentBlock = theBestParent || block.parentElement;

        if (!parentBlock || skipHtmlElements.includes(parentBlock.tagName.toLowerCase())) {
            continue;
        }

        // Remove duplicates
        if (!parentTexts.some(parentText => parentText.includes(parentBlock.textContent))) {
            parentTexts.push(parentBlock.textContent);
            parentHtmls.push(parentBlock.outerHTML);
            tags.push(parentBlock.tagName);
            documentElements.push(parentBlock)
        }
    }

    return {
        urlParsed: url,
        searchWord: searchedWord,
        searchAttributes: searchedAttributes,
        foundBlocks: parentTexts.length,
        htmlBlocks: parentHtmls,
        documentElements,
        tags: tags,
    };
}

function searchParentCriteria(tag, regularAttributesExpression) {
    // Method for searching correct parent (if possible) based on the attributes, class names, and so on
    const attrs = tag.attributes;

    for (let i = 0; i < attrs.length; i++) {
        const attrValue = attrs[i].value;
        if (attrValue instanceof Array) {
            for (let j = 0; j < attrValue.length; j++) {
                if (regularAttributesExpression.test(attrValue[j].toLowerCase())) {
                    return true;
                }
            }
        } else {
            if (regularAttributesExpression.test(attrValue.toLowerCase())) {
                return true;
            }
        }
    }

    return false;
}

export const stringToDOM = (html) => {
    const parser = new DOMParser()
    return parser.parseFromString(html, 'text/html')
}
